.container {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
}

.content-wrapper {
    flex-grow: 1;
    display: flex;
    justify-content: center;
}

Header {
    flex-shrink: 0;
}

Footer {
    flex-shrink: 0;
}

.input-box-wrapper {
    margin-top: auto;
    padding: 10px;
    width: 80%;
}